@import url(https://fonts.googleapis.com/css?family=Montserrat:300,300i,400,400i,500,500i,600,600i,700,700i,800,900);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 12px;
  color: #222229;
}

h1,
h2,
h3,
h4,
h5 {
  margin: 0.2em;
  font-weight: normal;
}

header,
nav,
footer,
section,
article,
div {
  box-sizing: border-box;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.structure {
  margin: 0.5em;
}

#game-wrapper {
  margin: 0em;
}

canvas {
  display: block;
  background: #2d3c5a;
  margin: 0.5em 0 0 0.5em;
  position: relative;
  z-index: 0;
}

#canvas-overlay {
  position: absolute;
  z-index: 1;
  background-color: transparent;
  cursor: crosshair;
}

button {
  background-color: #4c9f50;
  border: 1px solid #8CEF9A;
  color: white;
  padding: 0.3em 0.6em;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 1.2em;
  border-radius: 3px;
  -webkit-transition-duration: 0.1s;
          transition-duration: 0.1s;
  margin: 0.2em;
}

.extra {
  font-size: 1.1em;
  position: -webkit-sticky;
  position: sticky;
  text-align: right;
  right: 2em;
  bottom: 2em;
  width: 95%;
}

button:hover {
  background-color: white;
  color: black;
}

.showcase {
  position: relative;
  /* overflow: hidden; */
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  /* width: 100%; */
  /* text-align: center; */
  /* min-height: 18em; */
  margin: 0.5em 1em 0.5em;
  -webkit-transition-property: all;
  transition-property: all;
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s;
  -webkit-transition-timing-function: ease-out;
          transition-timing-function: ease-out;
  z-index: 0;
}

.game-interface {
  font-weight: 400;
  font-size: 1em;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-justify-content: center;
          justify-content: center;
  /* margin: 0em 1em 0.5em; */
  max-width: 50%;
  padding: 0.2em;
  position: relative;
}

.game-controls {
  font-weight: 400;
  font-size: 1em;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  margin: 0.1em 0.2em;
  padding: 0.1em 0.2em;
}

.interface-element {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 0 0.2em;
  margin: 0 0 0.4em;
}

form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  font-size: 1.1em;
  line-height: 1.3;
}

input[type=number] {
  font-size: 0.8em;
  width: 3em;
  text-align: center;
  vertical-align: baseline;
}

input[type=button] {
  font-size: 0.9em;
  font-family: Helvetica, sans-serif;
  text-align: center;
  margin: 0.2em 0 0;
  background-color: #02A2EF;
  padding: 3px;
  color: #fafafa;
}

p input[type=checkbox] {
  margin: auto 0.2em;
}

input[type=color] {
  margin: 0.2em;
}

input:disabled {
  background: #d6433a;
}

.tooltip {
  position: relative;
  display: inline-block;
}

/* Tooltip text */
.tooltip .tooltiptext {
  visibility: hidden;
  font-size: 0.8em;
  width: 8em;
  background-color: #546072;
  color: white;
  text-align: center;
  padding: 4px 0;
  border-radius: 5px;
  /* Position the tooltip text*/
  position: absolute;
  z-index: 1;
  bottom: 100%;
  left: 50%;
  margin-left: 3em;
  margin-top: 1em;
}

.tooltip .tooltiptext::after {
  content: " ";
  position: absolute;
  top: 50%;
  right: 100%; /* To the left of the tooltip */
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent #546072 transparent transparent;
}

/* Show the tooltip text when you mouse over the tooltip container */
td:hover .tooltiptext {
  visibility: visible;
}

.App {
  text-align: center;
  margin: 0.5em;
}

